import React from 'react';
import styles from './Accordion.module.scss';
import { MdKeyboardArrowRight } from 'react-icons/md';
import Footer from 'src/components/Footer/Footer';


export const Accordion = () =>{


    let dds;
    setTimeout(()=>{
        dds = document.querySelectorAll('dd');
    }, 2000)
    
    const handleClick = (e: any) =>{
        dds?.forEach((item, index)=>{
            item.classList.remove('active');
            item.style.display = 'none';
        })

        e.nextSibling.classList.add('active');
        e.nextSibling.style.display = 'block'
    }

    return(
            <section className={styles.faq}>
                <h2>FAQ - Perguntas frequentes</h2>
                
                <dl className={styles.faq__accordion}>
                    <dt onClick={(e)=>handleClick(e.target)} className={styles.faq__accordion__title}>
                        <p>As plataformas são autopropelidas?</p><label><MdKeyboardArrowRight style={{width: '30px', height: '30px'}}/></label>
                    </dt>
                    <dd className={styles.faq__accordion__text}>
                        Não. Nossas plataformas são inteiramente manuais ou de acionamento elétrico. Somente a Toucan T26E é autopropelida.
                    </dd>

                    <dt onClick={(e)=>handleClick(e.target)} className={styles.faq__accordion__title}>
                        <p>As plataformas operam em solo desnivelado?</p><label><MdKeyboardArrowRight style={{width: '30px', height: '30px'}}/></label>
                    </dt>
                    <dd className={styles.faq__accordion__text}>
                        Não, nossos equipamentos são para utilização em solo nivelado.
                    </dd>

                    <dt onClick={(e)=>handleClick(e.target)} className={styles.faq__accordion__title}>
                        <p>Qual a voltagem de operação das plataformas?</p><label><MdKeyboardArrowRight style={{width: '30px', height: '30px'}}/></label>
                    </dt>
                    <dd>
                        <ul style={{listStyle: 'none', marginLeft: '-2.5rem'}}>
                            <li>As Ecolift 50 e 70 são totalmente manuais.</li>
                            <li>A Liftpod FT 140 utiliza 110V.</li>
                            <li>A 1030P, 30AM e Toucan T26E utilizam 220V.</li>
                            <li>A 103OP DUO é bivolt.</li>
                        </ul>
                    </dd>

                    <dt onClick={(e)=>handleClick(e.target)} className={styles.faq__accordion__title}>
                        <p>As plataformas conseguem operar até que altura?</p><label><MdKeyboardArrowRight style={{width: '30px', height: '30px'}}/></label>
                    </dt>
                    <dd className={styles.faq__accordion__text}>
                        Nossas plataformas são Low Level Access, indicadas para trabalhos internos e externos até 10 metros de altura.
                    </dd>
                </dl>
            </section>
    );
}